<template>
  <div class="gallery">
    <div class="gallery__slider" >
        <div class="gallery__item" v-for="(item, index) in images" :key="index">
          <transition v-if="currentIndex == index" name="fade">
            <img @click=" big = item" :src="item" alt="" class="gallery__img"> 
          </transition>
        </div>
    </div>
    <button class="gallery__button button_prev" @click="prev()">&#10094;</button>
    <button class="gallery__button button_next" @click="next()">&#10095;</button>
  </div>
</template>

<script>
export default {
  props: [
    'images'
  ],
  data() {
    return {
      currentIndex: 0,
      transition: false,
      opened: false,
      big: '',
    }
  },
  mounted() {
    this.big = this.images[0]
    // this.getImageHeight()
  },  
  methods: {
    getImages(images, index) {
      return images.slice(index, index+4)
    },
    open() {
      if (!this.opened) {
        this.opened = true
      } else {
        this.opened = false
      }
      
    },
    getHeight(element){
      let styles = window.getComputedStyle(element)
      return parseInt(styles.height) + parseInt(styles.marginBottom) + parseInt(styles.marginTop)
    },
    next() {
        if (this.currentIndex < this.images.length) {
          this.currentIndex++
          console.log('curr: ' + this.currentIndex)
       }
    },
    prev() {
        if (this.currentIndex > 0) {
          this.currentIndex--
        }
    },
    getStep() {
      let item = window.getComputedStyle(this.$el.querySelector('.gallery__item-small'))
      return parseInt(item.height) + parseInt(item.marginBottom)
    },
    move(step) {
      let slider = this.$el.querySelector('.gallery__slider')
      let styles = window.getComputedStyle(slider)
      this.transition = true
      setTimeout(()=>{
        this.transition = false
      }, 500)
      slider.style.marginTop = parseInt(styles.marginTop) + step +  'px'
    }
  }
}
</script>

<style lang="scss">
.gallery {
  position: relative;
  width: 100%;
  overflow: hidden;

  &__slider {
    height: 100%;
    width: 100%;
    overflow: hidden;
  }

  &__item {
    max-width: 100%;
  }

  &__img {
    width: 100%;
  }

  &__button {
      cursor: pointer;
      position: absolute;
      top: 0;
      width: auto;
      padding: 16px;
      color: white;
      font-weight: bold;
      font-size: 18px;
      transition: 0.7s ease;
      border-radius: 0 4px 4px 0;
      text-decoration: none;
      user-select: none;
      background-color: rgba(0,0,0,0.05);
      border: none;
      height: 100%;
      width: 8%;
      outline: none;
      @media (min-width: 900px) { 
        &:hover {
          background-color: rgba(0,0,0,0.5);
          width: 10%;
        }
      }
  }

  .button_next {
    right: 0;
  }

  .button_prev {
    left: 0;
  }

  .fade-enter-active, .fade-leave-active {
    transition: all 0.5s ease;
    overflow: hidden;
    visibility: visible;
    position: absolute;
    opacity: 1;
  }

  .fade-enter, .fade-leave-to {
    visibility: hidden;
    width:100%;
    opacity: 0;
  }
}
</style>